.Navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid black;
    position: sticky;
    top: 0;
    background-color: #faf2e9;
    z-index: 10;

    font-family: 'aktiv-grotesk-std', sans-serif;
}

.Navbar-items {
    display: flex;
    flex-direction: row;
    font-size: 18px;
    font-weight: bold;
    padding: 15px;
}
